import {HttpClient,json} from 'aurelia-fetch-client';

/** shared struct among components
 *
 */
export class Bdapi {
  users = [];
  localapiurl = 'http://localhost:5001/egolem-8ee91/us-central1/api';
  remoteapiurl = 'https://us-central1-egolem-8ee91.cloudfunctions.net/api';


  constructor() {
    console.log('creating Bdapi()');
    this.client = new HttpClient();
    //this.apiurl = this.remoteapiurl;
    this.apiurl = (window.location.protocol === 'http:') ? this.localapiurl : this.remoteapiurl;
    let lfthis = this;
    this.client.configure(config => {
      config.useStandardConfiguration()
        .withBaseUrl(this.apiurl) //defining default api url
        .withInterceptor({ //interceptor adding authHeader
          request(request) {
            if (lfthis.token) {
              let authHeader = 'Bearer ' + lfthis.token;
              request.headers.append('Authorization', authHeader);
            } else {
              console.log('loginfb.interceptor() warning no token available. lfthis:',lfthis);
            }
            return request;
          }
        })
    })
  }

  checklogged(){
    console.log('bdapi.checklogged()')
    this.users = [];
    let s = document.cookie.indexOf('__session=');
    this.islogged = s>=0;
    if (this.islogged) {
      //parse cookies
      let cs = document.cookie.split(';')
        .map(v => v.split('='))
        .reduce((acc, v) => {
          acc[decodeURIComponent(v[0].trim())] = decodeURIComponent(v[1].trim());
          return acc;
        }, {});
      this.eguser = cs["__eguser"];
      this.token = cs["__session"];
      this.getusers();
    } else {
      this.eguser="";
      this.token = false;
    }
  }

  logout(){
    //TODO implement
    this.removeCookie('__session');
    this.removeCookie('__eguser');
    this.islogged=false;
  }

  removeCookie(sKey, sPath, sDomain) {
    document.cookie = encodeURIComponent(sKey) +
      "=; expires=Thu, 01 Jan 1970 00:00:00 GMT" +
      (sDomain ? "; domain=" + sDomain : "") +
      (sPath ? "; path=" + sPath : "");
  }

  getusers(){
    //get list of users
    this.client.fetch('/users/')
      .then(response => response.json())
      .then(data =>{
        this.users = data.users;
        //this.api.users = this.users;
        console.log('loginfb checklogged() /users data:',data);
      })

  }

  patchuser(user) {
    return this.client.fetch('/users/'+user.uid, {
      method: 'put',
      body: json(user)
    })
      .then(response => response.json())
      .then(savedUser => {
        console.log(`Saved user! ID: ${savedUser.uid}`);
        return savedUser;
      })
      .catch(error => {
        alert('Error saving comment!');
        console.error('bdapi patch error:',error);
      });
  }

}
