import firebase from 'firebase/compat/app';
import * as firebaseui from 'firebaseui'
import { getAnalytics } from "firebase/analytics";
import {inject} from "aurelia-dependency-injection";
import {Bdapi} from "./bdapi";

@inject(Bdapi)
export class Loginfb {

  constructor(bdapi){
    this.api = bdapi;
    console.log('loginfb()')
    this.firebaseConfig = {
      apiKey: "AIzaSyCrgGrjvDsFzsOjZIbiDCsbLlxgszSyQGw",
      authDomain: "egolem-8ee91.firebaseapp.com",
      projectId: "egolem-8ee91",
      storageBucket: "egolem-8ee91.appspot.com",
      messagingSenderId: "943106981786",
      appId: "1:943106981786:web:53689cfc408fd085c04423",
      measurementId: "G-MT0RZX9S32"
    };

    // Initialize Firebase

    this.app = firebase.initializeApp(this.firebaseConfig);
    this.analytics = getAnalytics(this.app);

  }

  attached(){
    console.log('loginfb() attached()',);
    var ui = new firebaseui.auth.AuthUI(firebase.auth());
    this.api.checklogged();
    this.islogged = this.api.islogged;
    let that = this;
    ui.start('#firebaseui-auth-container', {
      callbacks: {
        signInFailure: function(error) {
          // For merge conflicts, the error.code will be
          // 'firebaseui/anonymous-upgrade-merge-conflict'.
          if (error.code != 'firebaseui/anonymous-upgrade-merge-conflict') {
            return Promise.resolve();
          }
          // The credential the user tried to sign in with.
          var cred = error.credential;
          // Copy data from anonymous user to permanent user and delete anonymous
          // user.
          // ...
          // Finish sign-in after data is copied.
          return firebase.auth().signInWithCredential(cred);
        },
        signInSuccessWithAuthResult: function (authResult, redirectUrl) {
          // User successfully signed in.
          // Return type determines whether we continue the redirect automatically
          // or whether we leave that to developer to handle.
          console.log('login success', authResult);
          //window.egolem = {}
          //window.egolem.token =
          let email = authResult.user.email;
          let displayName = authResult.user.displayName;
          authResult.user.getIdToken().then(token=>{
            document.cookie = '__session=' + token + ';max-age=' + (3600*24*7) /* Persistent 1 week */
            document.cookie = '__eguser=' + (displayName?displayName:email) + ';max-age=' + (3600*24*7) /* Persistent 1 week */
          });
            //accessToken;
          // The signed-in user info.
          //window.egolem.uid = authResult.user.uid;
          //let email = authResult.user.email
          //console.log('firebase logged with authResult', authResult);
          //console.log('firebase logged with token: and user:', window.egolem.token, window.egolem.userinfo);
          //window.egolem.islogged = true;
          //console.log('firebase redirecturl:',redirectUrl);
          //this.islogged = true;
          that.api.checklogged();
          that.islogged= that.api.islogged;
          console.log('logged ?',that.islogged);
          return false; //redirect;
        }
      },
      signinFlow:'popup',
      signInSuccessUrl: 'books.html',
      autoUpgradeAnonymousUsers: true,
      signInOptions: [
        {
          provider: firebase.auth.EmailAuthProvider.PROVIDER_ID,
          requireDisplayName: false
        },
        firebase.auth.GoogleAuthProvider.PROVIDER_ID,
        firebase.auth.FacebookAuthProvider.PROVIDER_ID,
      ],
      tosUrl: 'about.html',
      privacyPolicyUrl:'about.html'
    });
  }

  logout(){
    this.api.logout()
    this.islogged=false;
  }

}
