import {inject} from "aurelia-dependency-injection";
import {Bdapi} from "./bdapi";
//import "font-awesome/css/font-awesome.css";

@inject(Bdapi)
export class Adminuser {

  constructor(bdapi){
    this.api=bdapi
  }

  attached() {
    //console.log('adminuser attached() loginfb:',this.loginfb);
  }

  editroles(user) {
    console.log('editroles() user:',user);
    this.edituser = {...user};
    this.edituserlink = user;
    this.editing=true;
  }

  submit(){
    this.api.patchuser(this.edituser);
    //this.edituser = null;
    this.editing=false;
  }

  cancel(){
    this.edituser=null;
    this.editing=false;
  }
}
